<template>
  <div class="question-answer">
    <div class="answer-head">
      <!-- <div class="head-line"></div> -->
      <div>
        <div class="head-title">常見問題</div>
		<div class="head-sub">Questions and Answers</div>
      </div>
    </div>
    <div class="answer-main">
			<div class="main-box"
				v-for="(subject, index) in subjects"
				:key="index"
			>
				<div class="main-subject">
					<div class="main-head">{{ subject.head }}</div>
					<div v-if="!subject.switch" @click='switchOn(index)' class="main-icon">–</div>
					<div v-else @click='switchOn(index)' class="main-icon">+</div>
				</div>
				<div v-if="!subject.switch" class="main-text">{{ subject.text }}</div>
			</div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'QuestionAnswer',
  components: {

  },
  data () {
    return {
      subjects: [
        { switch: true, head: '如果我完全沒有設計相關經驗，適合上學院課程嗎？', text: 'Reborn 設計學院課程針對零基礎學生也設置了一套教學系統。可以透過額外安排的練習與作業，協助基礎較弱的學生同時掌握設計思維脈絡與設計實作能力，歡迎你從現在開始加入我們！' },
        { switch: true, head: '學院課程只會教 UI 介面設計而已嗎？', text: '課程將以產品設計思維與 UX 使用者體驗為中心，引導你一步步打造原創產品 UI 設計作品集，教你有憑有據的打造設計作品，並結合實體工作坊和龐大的設計社群，一次繳費終生學習，是不是有點心動？' },
        { switch: true, head: '為什麼選擇 Figma 為主要教學使用設計工具，而不是 Sketch 或 Adobe XD？', text: '依據 UXtools 2020 年度使用調查報告指出，Figma 是目前國內外業界使用頻率最高的設計軟體，為了讓同學快速上手主流工具，我們將大量使用 Figma 教學，讓每位學生熟練此軟體，並著手開始設計專屬的原創產品作品集。' },
        { switch: true, head: '學院課程中間的作業份量會很重嗎？通常要花多少時間完成？', text: '每個人分配的學習時間不一樣，但如果你抱著轉職的決心，這段期間跟著 Reborn 設計學院就業系統課程學習，可以減少你自學耗費多餘的時間，更有效率的打造出就職水準的產品設計作品集，這成果絕對值得你努力去嘗試的。' },
        { switch: true, head: '如果上課期間有事需要請假怎麼辦？我要如何補課？', text: '請假只需要在班級群組跟我們的助教說一聲即可。我們的每一堂課都有錄影，課後提供回放檔連結給同學們，請盡量在下堂直播課程開始前看完，但不強制。' },
        { switch: true, head: '報名後可以退費嗎？我要如何申請退費？', text: '課程將以小班制進行教學，為了保障其他學生權益，購買課程（含當天）7 日內可退還全額課程費用；超過 7 日後申請恕不退費。你可以透過聯繫客服提出退費申請。' }
      ]
    }
  },
  methods: {
    switchOn (index) {
      this.subjects[index].switch = !this.subjects[index].switch
    }
  }
}
</script>
<style lang="scss" scoped>
.question-answer {
    max-width: 1170px;
	margin: auto;
	padding: 40px 48px 130px;

    .answer-head {
        text-align: center;

        .head {

            &-line {
                width: 5px;
                height: 64px;
                background-color: #25282B;
                margin-right: 22px;
            }

            &-title {
                font-size: 36px;
                margin-bottom: 10px;
                font-weight: bold;
            }

			&-sub {
				font-size: 17px;
				color: #878C92;
				margin-top: 6px;
			}
        }
    }

		.answer-main {
			margin-top: 94px;

			.main {

				&-box {
					font-size: 20px;
					padding: 24px 0px;
					border-bottom: 1px solid #C8CCD1;
				}

				&-subject {
					display: flex;
					justify-content: space-between;
				}

				&-head {
					color: #25282B;
				}

				&-icon {
					width: 20px;
					height: 20px;
					text-align: center;
					cursor: pointer;
				}

				&-text {
					font-size: 14px;
					margin-top: 14px;
					color: #646B71;
					padding-right: 20px;
					line-height: 24px;
				}
			}
		}
}

@media( min-width: 1260px ){
    .reborn-workshop {

    }
}

@media( max-width: 1023px ){

	.question-answer {
			padding: 56px 20px 100px;

			.answer-head {
				align-items: center;

					.head {

							&-line {
								height: 40px;
							}

							&-title {
								font-size: 32px;
							}
					}
			}

			.answer-main {
				margin-top: 30px;
				.main {

					&-box {

					}

					&-subject {

					}

					&-head {

					}

					&-icon {

					}

					&-text {

					}
				}
			}
	}

}

</style>



